@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.carousel-button {
  border-radius: 50%;
}

.carousel-button:hover {
  background-color: #ddd;
  color: black;
}

#carousel {
  width: 500px;
}

.intro {
  display: inline-flex;
  width: auto;
  height: auto;
  margin: 0 auto;
  /* padding-top: 25%; */
}
.intro h1 {
  z-index: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 50px;
  color: #616161;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid #14b8af; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  -webkit-animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
          animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}

.color {
  background-color: #14b8af;
}
.words {
  font-family: "Quicksand", sans-serif;
  font-size: 7vh;
  color: white;
  word-wrap: break-word;
  width: 45vw;
  max-height: 50vh;
}

.wordColorSize {
  font-family: "Quicksand", sans-serif;
  font-size: 50px;
  color: white;
}

.landing {
  align-items: center;
  justify-content: space-around;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  height: 85vh;
  width: 100vw;
}

.headshot {
  float: left;
  max-height: 50vh;
}

.homeSection {
  align-items: center;
  justify-content: space-around;
  display: flex;
  flex-flow: row wrap;
  height: 120vh;
  width: 100vw;
}

.homeSection.color {
  background-color: #14b8af;
}

.homeSection.bg {
  background-color: white;
  overflow: hidden;
  display: inline-block;
  padding: 10px;
  font-weight: bold;
  font-family: arial;
  color: transparent;
  font-size: 9.5vw;
  font-family: "Quicksand", sans-serif;
}
.homeSection.bg-text {
  background-image: url(/marjan-personal-website/static/media/t2.1b42f922.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.rotateLeft {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}

.rotateRight {
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}
.word.fall-text {
  opacity: 0;
  visibility: hidden;
  padding: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 50px;
  color: white;
  position: relative;
  -webkit-transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out, -webkit-transform 1.2s ease-out;
  will-change: opacity, visibility;
}

.word.fall-text.fall1 {
  -webkit-animation-name: movingTopToBottom;
          animation-name: movingTopToBottom;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  opacity: 1;
  visibility: visible;
}

.word.fall-text.fall2 {
  -webkit-animation-name: movingTopToBottom;
          animation-name: movingTopToBottom;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  opacity: 1;
  visibility: visible;
}

.word.fall-text.fall3 {
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
  -webkit-animation-name: movingTopToBottom;
          animation-name: movingTopToBottom;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  opacity: 1;
  visibility: visible;
}

.fade-in-section {
  opacity: 0;
  -webkit-transform: translateY(20vh);
          transform: translateY(20vh);
  visibility: hidden;
  -webkit-transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out, -webkit-transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  visibility: visible;
  -webkit-transform: none;
          transform: none;
}

@-webkit-keyframes movingTopToBottom {
  0% {
    top: -500px;
  }
  100% {
    top: 5px;
    opacity: 100;
  }
}

@keyframes movingTopToBottom {
  0% {
    top: -500px;
  }
  100% {
    top: 5px;
    opacity: 100;
  }
}

/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #14b8af;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #14b8af;
  }
}

#nav-container {
  width: 100%;
  background: transparent;
  float: right;
  z-index: 1;
}
nav {
  height: 30px;
  padding: 0;
  margin: 0;
}
nav ul {
  top: 0;
  float: right;
  list-style-type: none;
  margin: 0;
  text-align: center;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  justify-content: center;
  align-items: center;
  background: transparent;
  -webkit-transition: top 0.3s;
  transition: top 0.3s;
}

nav li {
  float: top;
  margin: 25px;
  padding-right: 20px;
}
nav li a {
  display: inline-block;
  color: #14b8af;
  font-family: "Quicksand", sans-serif;
  font-weight: 1000;
  margin: 10%;
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  text-align: center;
  align-content: center;
}
nav li a:hover {
  animation: bounce 2.5s infinite;
  -webkit-animation: bounce 2.5s infinite;
  -moz-animation: bounce 2.5s infinite;
  -o-animation: bounce 2.5s infinite;
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
  }
}
@keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

